import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

export default function Home() {
  const [tokens, setTokens] = useState(0);
  const [inventory, setInventory] = useState([]);
  const [currentDrop, setCurrentDrop] = useState(null);
  const [isOpening, setIsOpening] = useState(false);

  const drops = [
    { name: "Common Item", rarity: "Common", value: 5, chance: 0.60, color: "text-gray-500" },
    { name: "Rare Item", rarity: "Rare", value: 20, chance: 0.25, color: "text-blue-500" },
    { name: "Epic Item", rarity: "Epic", value: 50, chance: 0.10, color: "text-purple-500" },
    { name: "Legendary Item", rarity: "Legendary", value: 100, chance: 0.05, color: "text-yellow-500" },
  ];

  const getRandomDrop = () => {
    const rand = Math.random();
    let cumulative = 0;
    for (const drop of drops) {
      cumulative += drop.chance;
      if (rand <= cumulative) return drop;
    }
    return drops[0]; // Fallback to Common
  };

  const handleOpenCase = () => {
    if (isOpening) return;
    setIsOpening(true);
    setCurrentDrop(null);

    // Simulate scrolling by generating a list of random drops
    setTimeout(() => {
      const finalDrop = getRandomDrop();
      setCurrentDrop(finalDrop);
      setIsOpening(false);
    }, 3000); // 3-second scroll animation
  };

  const handleSaveDrop = () => {
    if (currentDrop) {
      setInventory([...inventory, currentDrop]);
      setCurrentDrop(null);
    }
  };

  const handleSellDrop = () => {
    if (currentDrop) {
      setTokens(tokens + currentDrop.value);
      setCurrentDrop(null);
    }
  };

  const handleSellFromInventory = (index) => {
    const item = inventory[index];
    setTokens(tokens + item.value);
    setInventory(inventory.filter((_, i) => i !== index));
  };

  // Generate fake drops for scrolling effect
  const scrollingDrops = Array(20)
    .fill(null)
    .map(() => drops[Math.floor(Math.random() * drops.length)]);

  return (
    <div className="min-h-screen bg-gray-100 flex items-center justify-center p-4">
      <div className="bg-white p-6 rounded-lg shadow-lg text-center max-w-lg w-full">
        <h1 className="text-3xl font-bold mb-4">Free Case Gambling</h1>
        <p className="text-lg mb-4">Tokens: <span className="font-semibold">{tokens}</span></p>

        {/* Free Case Button */}
        <button
          onClick={handleOpenCase}
          className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600 transition disabled:opacity-50"
          disabled={isOpening}
        >
          Open Free Case
        </button>

        {/* Scrolling Drop Animation */}
        {isOpening && (
          <div className="mt-6 h-20 overflow-hidden relative border border-gray-300 rounded">
            <motion.div
              className="flex space-x-4"
              initial={{ x: "100%" }}
              animate={{ x: "-100%" }}
              transition={{ duration: 3, ease: "linear" }}
            >
              {scrollingDrops.map((drop, index) => (
                <div
                  key={index}
                  className={`flex-shrink-0 w-20 p-2 text-center ${drop.color}`}
                >
                  {drop.name}
                </div>
              ))}
            </motion.div>
            <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
              <div className="w-1 h-16 bg-red-500"></div>
            </div>
          </div>
        )}

        {/* Drop Result */}
        <AnimatePresence>
          {currentDrop && !isOpening && (
            <motion.div
              className="mt-6"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
            >
              <h2 className="text-xl font-semibold">You Got:</h2>
              <p className={`text-lg my-2 ${currentDrop.color}`}>
                {currentDrop.name} ({currentDrop.rarity}, {currentDrop.value} tokens)
              </p>
              <div className="flex justify-center space-x-4">
                <button
                  onClick={handleSaveDrop}
                  className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
                >
                  Save to Inventory
                </button>
                <button
                  onClick={handleSellDrop}
                  className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600 transition"
                >
                  Sell Drop
                </button>
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        {/* Inventory */}
        <div className="mt-6">
          <h2 className="text-xl font-semibold">Inventory</h2>
          {inventory.length === 0 ? (
            <p className="text-gray-600">Nothing yet!</p>
          ) : (
            <ul className="mt-2 space-y-2">
              {inventory.map((item, index) => (
                <li
                  key={index}
                  className="flex justify-between items-center bg-gray-100 p-2 rounded"
                >
                  <span className={item.color}>
                    {item.name} ({item.rarity}, {item.value} tokens)
                  </span>
                  <button
                    onClick={() => handleSellFromInventory(index)}
                    className="bg-red-500 text-white px-2 py-1 rounded hover:bg-red-600 transition"
                  >
                    Sell
                  </button>
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
}